import { AcceptedEmoji } from './Message'

export interface Emoji {
    type: 'emoji'
    emoji: string
}

export function Emoji(emoji: AcceptedEmoji){
    return { type: 'emoji', emoji }
}

export interface CustomEmoji {
    type: 'custom_emoji',
    custom_emoji_id: string
}

export function CustomEmoji(custom_emoji_id: string){
    return { type: 'custom_emoji', custom_emoji_id }
}

export interface PaidEmoji {
    type: 'paid'
}

export function PaidEmoji() {
    return { type: 'paid' }
}